.wrapper {
	position: relative;
	height: 100%;
	.video__container {
		height: 100%;
		.video {
			width: 100%;
			height: 100%;
			object-fit: contain;
			object-position: center;
			transform: scaleX(-1);

			&__noScaled {
				transform: scaleX(1);
			}
		}
	}

	.actions {
		position: absolute;
		bottom: 20px;
		left: 0;
		right: 0;
		padding: 10px;
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 30px;

		.action {
			width: 50px;
			height: 50px;
			cursor: pointer;

			&__file {
				* {
					stroke: #ffffff;
				}
			}

			* {
				stroke-width: 15px;
			}
		}

		.button {
			position: relative;
			width: 60px;
			height: 60px;
			border-radius: 50%;
			background-color: #ffffff;
			transition: all 0.3s;
			border: 3px solid #ff485c;
			padding: 3px;

			&__active {
				background: linear-gradient(
					125.88deg,
					#ff6644 -2.1%,
					#ff485c 100.96%,
					#ff1a6c 127.3%
				);
			}
		}
	}
}
